/* selects each cloud for styling */
.cloud {
    position: absolute;
    width: 200px;
    top: 120px;
    opacity: 0.5;
    animation: wind 100s linear infinite reverse;
}
.cloud2 {
   position: absolute; 
  width: 300px;
  top: 90px;
  opacity: 0.6;
  animation: wind 75s linear infinite reverse;
  
}


  
  /*// KEYFRAMES the animation controls //*/
